import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';

import {
  Container,
  Title,
  WrapperFilters,
  WrapperPanel,
  Panel,
  WrapperLeft,
  HandIcon,
  WrapperCenter,
  WrapperRight,
  ArrowIcon,
  InfoLeft,
  InfoRight,
  TitleInfo,
  WrapperInfo,
  TextInfo
} from './styles';

interface IQuote {
  id: string;
  idStatus: string;
  status: string;
  dtEmission: string;
  dtLimit: string;
}

const Quotes: React.FC = () => {
  const [quotes, setQuotes] = useState<IQuote[]>([]);

  const mokkaQuotes = [
    {
      id: '000134',
      idStatus: '0',
      status: 'Aberto',
      dtEmission: '22/03/2020',
      dtLimit: '22/04/2020'
    },
    {
      id: '000133',
      idStatus: '1',
      status: 'Finalizado',
      dtEmission: '22/01/2020',
      dtLimit: '22/02/2020'
    },
    {
      id: '000132',
      idStatus: '2',
      status: 'Vencido',
      dtEmission: '22/01/2020',
      dtLimit: '22/02/2020'
    }
  ];

  useEffect(() => {
    setQuotes(mokkaQuotes);
  }, []);

  return (
    <Container>
      <Title>Titulo</Title>
      <WrapperFilters />
      <WrapperPanel>
        {quotes.map(quote => (
          <Link key={quote.id} to={`/quotesDetail/quoteId=${quote.id}`}>
            <Panel>
              <WrapperLeft>
                <HandIcon />
              </WrapperLeft>
              <WrapperCenter>
                <WrapperInfo>
                  <InfoLeft>
                    <TitleInfo>cód</TitleInfo>
                    <TextInfo>{quote.id}</TextInfo>
                  </InfoLeft>
                  <InfoRight>
                    <TitleInfo>status</TitleInfo>
                    <TextInfo className={`color${quote.idStatus}`}>
                      {quote.status}
                    </TextInfo>
                  </InfoRight>
                </WrapperInfo>
                <WrapperInfo>
                  <InfoLeft>
                    <TitleInfo>Dt. Emissão</TitleInfo>
                    <TextInfo>{quote.dtEmission}</TextInfo>
                  </InfoLeft>
                  <InfoRight>
                    <TitleInfo>Dt. Limite</TitleInfo>
                    <TextInfo>{quote.dtLimit}</TextInfo>
                  </InfoRight>
                </WrapperInfo>
              </WrapperCenter>
              <WrapperRight>
                <ArrowIcon />
              </WrapperRight>
            </Panel>
          </Link>
        ))}
      </WrapperPanel>
    </Container>
  );
};

export default Quotes;
